<script>
/** 全域翻譯
 *  https://github.com/i7eo/google-translate-select
 *  https://zh.wikipedia.org/wiki/ISO_639-1
 * */
import { ref, onMounted, watch } from "vue"
import { RouterLink, RouterView } from "vue-router"
import PluginExample from "@/components/loading/pluginExample.vue"
import { useTranslateLanguageStore } from "@/store/global"
import { storeToRefs } from "pinia"


export default {
  setup() {

    const TranslateLanguageStore = useTranslateLanguageStore()
    const { showTranslate } = storeToRefs(TranslateLanguageStore)
    onMounted(() => {
      // setTimeout(() => (showTranslate.value = true), 1000)
    })

    const languages = [
      {
        code: "en",
        name: "English",
        cname: "英語",
        ename: "English",
      },
      {
        code: "zh-CN",
        name: "Chinese (Simplified)",
        cname: "中文 (简体)",
        ename: "Chinese (Simplified)",
      },
      {
        code: "zh-TW",
        name: "Chinese (Traditional)",
        cname: "中文 (繁体)",
        ename: "Chinese (Traditional)",
      },
      {
        code: "id",
        name: "Indonesia",
        cname: "印度尼西亚語",
        ename: "Indonesian",
      },
      {
        code: "ja",
        name: "にほんご",
        cname: "日語",
        ename: "Japanese",
      },
      {
        code: "ko",
        name: "한국어",
        cname: "韩語",
        ename: "Korean",
      },
      {
        code: "ms",
        name: "Malay",
        cname: "马来西亚語",
        ename: "Malay",
      },
      {
        code: "th",
        name: "ภาษาไทย",
        cname: "泰語",
        ename: "Thai",
      },
      {
        code: "vi",
        name: "Việt Ngữ",
        cname: "越語",
        ename: "Vietnam",
      },
    ]
    const handleGoogleTranslateSelect = (language) => {
      console.log(JSON.stringify(language))
    }
    const closeModal = () => {
      showTranslate.value = false
    }

    return {
      handleGoogleTranslateSelect,
      showTranslate,
      languages,
      closeModal,
    }
  },
  components: {
    PluginExample,
  },
}
</script>

<template>
  <div id="app" class="no-select">
    <div
      class="custom-modal disable-touch"
      ref="myModalRef"
      id="MyModal"
      v-show="showTranslate"
    >
      <div class="modal-dialog d-flex justify-content-center">
        <div class="modal-content custom-modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="MyModalLabel"></h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
              @click="closeModal"
            ></button>
          </div>
          <div class="modal-body" style="padding: 0px">
            <!--  -->
            <div class="" style="">
              <GoogleTranslateSelect
                :languages="languages"
                default-language-code="zh-TW"
                default-page-language-code="zh-TW"
                :showArrow="false"
                :fetch-browser-language="false"
                @select="handleGoogleTranslateSelect"
                trigger="click"
              />
            </div>
            <!--  -->
          </div>
          <div class="modal-footer">
            <!-- <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              確定
            </button> -->
          </div>
        </div>
      </div>
    </div>
    <router-view />
    <PluginExample />
  </div>
</template>

<style lang="scss" scoped>
/* https://developer.apple.com/forums/thread/133399 */
.no-select {
  -webkit-touch-callout: none; /* Safari Touch */
  -webkit-user-select: none; /* Webkit */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Edge*/
  user-select: none; /* Future-proof*/
}
.no-drag {
  -webkit-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

.custom-modal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999;

  .custom-modal-content {
    width: 80vw !important;
    height: 50vh !important;
  }
}

</style>
