import Swal from 'sweetalert2';

const color = process.env.VUE_APP_MODE == 'purple' ? '#8f63cf':'#f8777d'

export const Toast = (text) => {
    const result = Swal.mixin({
        confirmButtonColor: color
    });
    return result.fire(text)
}


const confirmConfig = {
    confirmButtonColor:color,
    showCancelButton: true,
    cancelButtonText: "取消",
    confirmButtonText: "確定"
}

//確定/取消
export const ToastConfirm = (text) => {
    const result = Swal.mixin({
        ...confirmConfig
    });
    return result.fire(text).then((result) => {
        if (result.isConfirmed) {
            return true;
        } else {
            return false;
        }
    });
}

//輸入框
export const ToastInputConfirm = (text = "", placeHolder = "") => {
    return Swal.fire({
        ...confirmConfig,
        title: text,
        input: 'textarea',
        inputLabel: '',
        inputPlaceholder: placeHolder,
        inputAttributes: {
            'aria-label': 'Type your message here'
        },
    })
}